html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
  text-transform: uppercase;
  margin: 2rem 0 5rem 0;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
  font-weight: 700;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

a {
  color: #FFFFFF;
}

ul {
  list-style: none;
}

.boton-azul {
  background-color: #2B3B8E;
  padding: 0.5rem 2rem;
  margin: 2rem auto 2rem auto;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
}
@media (min-width: 768px) {
  .boton-azul {
    width: auto;
    margin: 0;
  }
}
.boton-azul:hover {
  background-color: #1f2b67;
  cursor: pointer;
}

.boton-amarillo {
  background-color: rgba(232, 237, 12, 0.8980392157);
  padding: 0.5rem 2rem;
  margin: 2rem auto 2rem auto;
  color: #1a1b15;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
}
@media (min-width: 768px) {
  .boton-amarillo {
    width: auto;
    margin: 0;
  }
}
.boton-amarillo:hover {
  background-color: rgba(184, 188, 10, 0.8980392157);
  cursor: pointer;
}

.boton-rojo {
  background-color: #cb0000;
  padding: 0.5rem 2rem;
  margin: 2rem auto 2rem auto;
  color: #1a1b15;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
}
@media (min-width: 768px) {
  .boton-rojo {
    width: auto;
    margin: 0;
  }
}
.boton-rojo:hover {
  background-color: #980000;
  cursor: pointer;
}

.formulario {
  margin: 0 auto;
  width: 70%;
}

.campo {
  margin-bottom: 2rem;
}
.campo label {
  display: block;
  width: 100%;
  margin-bottom: 1rem;
}
.campo input {
  width: 100%;
  padding: 1.25rem;
}

textarea {
  display: block;
  height: 10rem;
  max-height: 10rem;
  width: 100%;
  margin-bottom: 2rem;
}

select {
  width: 100%;
  text-align: center;
}

.header h1 {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .header h1 {
    font-size: 4rem;
  }
}

.header {
  padding: 2rem;
  background-color: #2B3B8E;
}
.header .header-logo {
  color: #FFFFFF;
}

.header-contenedor {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.header-navegacion {
  display: flex;
  align-items: center;
}
.header-navegacion a {
  margin-right: 1.2rem;
}

.abrir-menu {
  display: none;
}

.cerrar-menu {
  display: none;
}

@media screen and (max-width: 768px) {
  .abrir-menu,
  .cerrar-menu {
    display: block;
    border: 0;
    font-size: 3rem;
    background-color: transparent;
    cursor: pointer;
  }
  .header-navegacion {
    display: none;
    visibility: hidden;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #2B3B8E;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: end;
    gap: 2rem;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.5);
  }
  .nav-visible {
    opacity: 1;
    visibility: visible;
  }
  .nav-list {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 2rem;
  }
  .nav-list a {
    color: #FFFFFF;
  }
}
.footer {
  margin-top: 5rem;
  padding: 2.5rem;
  background-color: #2B3B8E;
}
.footer p {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .footer {
    display: flex;
    justify-content: space-between;
  }
  .footer .datos {
    flex-direction: row;
  }
}

.nav-list-footer {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.titulo-home {
  margin-top: 5rem;
  margin-bottom: 10rem;
}

.botones {
  display: flex;
  margin-top: 5rem;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 4rem;
}
@media (min-width: 768px) {
  .botones {
    flex-direction: row;
    gap: 5rem;
  }
}

.mis-puntos h2 {
  margin-top: 5rem;
}

.totales .totales-kaizen {
  margin-bottom: 5rem;
  padding: 3rem;
}
@media (min-width: 768px) {
  .totales {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}

.canjes {
  text-align: center;
}
.canjes .canjes-premio {
  margin: 0 auto;
  padding: 3rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.74);
}
@media (min-width: 768px) {
  .canjes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .canjes .canjes-premio {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .canjes .canjes-premio-descripcion {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    gap: 2rem;
  }
}

.kaizen {
  margin-top: 3rem;
  padding: 5rem;
  justify-content: space-between;
  border-bottom: 1px solid rgba(0, 0, 0, 0.37);
}
@media (min-width: 768px) {
  .kaizen {
    display: flex;
  }
}
@media (min-width: 768px) {
  .kaizen .kaizen-descripcion {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.kaizen .kaizen-descripcion span {
  font-weight: 700;
}
.kaizen .kaizen-botones {
  text-align: center;
}
@media (min-width: 768px) {
  .kaizen .kaizen-botones {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
}

.boton-volver {
  width: 70%;
  margin: 0 auto;
  margin-bottom: 2.5rem;
}